.bg-primary-light {
  background-color: #F5DADF !important;
}

a.bg-primary-light:hover, a.bg-primary-light:focus,
button.bg-primary-light:hover,
button.bg-primary-light:focus {
  background-color: #eab2bc !important;
}

.sticky-bottom {
  top: auto;
  bottom: 1.25em;
}

.sticky-bottom__container {
  display: flex;
  align-items: flex-end;
}

.hover-overlay {
  position: relative;
}

.hover-overlay:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: background-color 0.15s ease-in-out;
}

.hover-overlay:hover:after {
  background-color: rgba(255, 255, 255, 0.2);
}

.btn-unstyled {
  border: 0;
  padding: 0;
  background: none;
}

.btn-unstyled:focus {
